@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;600&display=swap);
html {
  font-size: 62.5%;
  line-height: 1.5;
}

body {
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* color pallete */
:root {
  --color-primary: hsl(0, 93%, 68%);
  --color-secondary: hsl(0, 36%, 70%);
  --color-accent: hsl(0, 6%, 24%);
  --color-white: #eee;
  --gradient-primary: linear-gradient(
    135deg,
    hsl(0, 0%, 100%),
    hsl(0, 100%, 98%)
  );
  --gradient-secondary: linear-gradient(
    135deg,
    hsl(0, 80%, 86%),
    hsl(0, 74%, 74%)
  );
}

.container {
  padding: 3rem 1rem;
}

@media screen and (min-width: 400px) {
  .container {
    padding: 3rem 4rem;
  }
}

@media screen and (min-width: 500px) {
  .container {
    padding: 3rem 8rem;
  }
}

@media screen and (min-width: 768px) {
  
  .container {
    padding: 3rem 8rem;
  }
}

@media screen and (min-width: 900px) {
  
  .container {
    padding: 3rem 10rem;
  }
}

h1,
h2,
h3 {
  margin-bottom: 1rem;
}

/* Grid */
.row {
  display: grid;
  grid-template-columns: 1fr;
}

@media screen and (min-width: 768px) {
  .row {
    grid-template-columns: 60% 40%;
  }

  .col-nav {
    grid-area: 1/1/2/2;
  }

  .col-img {
    grid-area: 1/2/3/3;
  }

  .col-details {
    grid-area: 2/1/3/2;
  }
}

/*** Hero Image ***/
.hero__image--mobile {
  width: 100%;
}

.hero__image--desktop {
  display: none;
}

@media screen and (min-width: 768px) {
  .hero__image--mobile {
    display: none;
  }

  .hero__image--desktop {
    display: block;
    height: 100vh;
    width: 100%;
    object-fit: cover;
  }
}

/*** Details ****/
.details {
  text-align: center;
}

.heading {
  font-size: 4rem;
  letter-spacing: 15px;
  font-weight: 600;
  -webkit-transform:translate(-500px, -200px);
          transform:translate(-500px, -200px);
  -webkit-animation: comein 1s ease-out forwards;
          animation: comein 1s ease-out forwards;
}

.heading span {
  font-weight: 300;
  color: hsl(0, 36%, 70%);
  color: var(--color-secondary);
}

.text {
  color: hsl(0, 36%, 70%);
  color: var(--color-secondary);
}

@-webkit-keyframes comein {
  to {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@keyframes comein {
  to {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@media screen and (min-width: 768px) {
  .details {
    text-align: left;
  }
  
}

/*** Input ***/
.input-group {
  margin-top: 4rem;
  border: 1px solid hsl(0, 36%, 70%);
  border: 1px solid var(--color-secondary);
  display: -webkit-inline-flex;
  display: inline-flex;
  border-radius: 5rem;
  width: 100%;
  -webkit-transform:translate(-500px);
          transform:translate(-500px);
  -webkit-animation: comein 1s ease-out forwards;
          animation: comein 1s ease-out forwards;

}

.input-group:focus-within {
  box-shadow: 0 0 3px hsl(0, 93%, 68%);
  box-shadow: 0 0 3px var(--color-primary);
}

.input {
  -webkit-flex: 1 1;
          flex: 1 1;
  border: 0;
  padding: 1.2rem 2rem;
  outline: 0;
  border-radius: inherit;
}

.btn {
  outline: 0;
  border: 0;
  cursor: pointer;
}

.btn-input {
  border-radius: inherit;
  width: 8rem;
  color: #eee;
  color: var(--color-white);
  background-image: linear-gradient(
    135deg,
    hsl(0, 80%, 86%),
    hsl(0, 74%, 74%)
  );
  background-image: var(--gradient-secondary);
}

.btn-input:hover {
  opacity: 0.8;
  box-shadow: 0 0 5px hsl(0, 36%, 70%);
  box-shadow: 0 0 5px var(--color-secondary);
}

::-webkit-input-placeholder {
  color: hsl(0, 36%, 70%);
  color: var(--color-secondary);
  opacity: 0.5;
}

:-ms-input-placeholder {
  color: hsl(0, 36%, 70%);
  color: var(--color-secondary);
  opacity: 0.5;
}

::placeholder {
  color: hsl(0, 36%, 70%);
  color: var(--color-secondary);
  opacity: 0.5;
}

.error {
  padding-left: 1.2rem;
  color: hsl(0, 93%, 68%);
  color: var(--color-primary);
  margin-bottom: 4rem;
}
